.slider__toggles {
  width: 65%;
  max-width: 280px;
  align-items: center;
  margin-top: 15px;
  padding-bottom: 40px;
  @media (min-width: @tablet-width) {
    max-width: 590px;
  }
  .desktop({
    width: 100%;
  })
}

.slider__bar {
  width: 40%;
  height: 14px;
  background-color: @white;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
  position: relative;
  cursor: pointer;
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.slider__bar--fat::before {
  content: "";
  position: absolute;
  width: 50%;
  height: 6px;
  background-color: @basic-green;
  border-radius: 6px;
  margin: 3px;
  left: 0;
}

.slider__bar--slim::before {
  content: "";
  position: absolute;
  width: 50%;
  height: 6px;
  background-color: @basic-green;
  border-radius: 6px;
  margin: 3px;
  right: 0;
}

.slider__cat--hide {
  position: absolute;
  opacity: 0;
}

.slider__cat--show {
  position: static;
  opacity: 1;
}

.slider__prev, .slider__next {
  padding: 0;
  cursor: pointer;
  @media (min-width: @tablet-width) {
    border-bottom: 1px dashed rgba(0, 0, 0, 0.199);
    &:hover {
      border-bottom: 1px solid rgba(0, 0, 0, 0.199);
    }

    &:active {
      border-bottom-color: transparent;
    }
  }
}

/* range slider */

.slider__range {
  display: none;
  @media (min-width: @tablet-width) {
    display: flex;
    width: 70%;
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
    cursor: pointer;

  }
  &::-webkit-slider-runnable-track {
    background-color: #DCDCDC;
    height: 6px;
    border-radius: 6px;
  }

  &::-moz-range-track {
    background-color: #DCDCDC;
    height: 6px;
    border-radius: 6px;
  }

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 34px;
    height: 34px;
    background-color: @basic-green;
    border: 11px solid @white;
    box-shadow: 0px 0px  0px 1px #cdcdcd,
                0px 1px 3px 1px #cdcdcd;
    margin-top: -14px;
    border-radius: 50%;
    position: relative;
    &:hover {

      box-shadow:0px 0px  0px 1px #cdcdcd,
      0px 1px 3px 1px #cdcdcd,
       0px 2px 4px 0px rgba(0, 0, 0, 0.19);
    };

    &:active {
      box-shadow:0px 0px  0px 1px @basic-green,
      0px 1px 3px 1px #cdcdcd,
       0px 2px 4px 0px rgba(0, 0, 0, 0.19);
    };
  }

  // &::-webkit-slider-thumb::before {
  //   content: "";
  //   position: absolute;
  //   width: 8px;
  //   height: 8px;
  //   top: 0;
  //   left: 0;
  //   background-color: @basic-green;
  //   border-radius: 50%;

  // }
}


