.program-section {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 20px;
  .main-font();
  @media (min-width: @tablet-width) {
    min-width: 710px;
  }


  @media (min-width: @desktop-width) {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  .desktop-wrapper;
  padding: 0;
  margin-bottom: 70px;
  }

}

.program-section__card {
  background-color: @basic-grey;
  width: auto;
  margin: 0 auto;
  padding: 20px;
  @media (min-width: @tablet-width) {
    padding-left: 50px;
    padding-bottom: 50px;
  };
  .desktop({
    width: 570px;
  })
}

.program-section__card + .program-section__card {
  margin-top: 20px;
  .desktop({
    margin-top: 0;
  })
}

.program-section__title {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 24px;
  padding-left: 70px;
  position: relative;
  @media (min-width: @tablet-width) {
    padding-left: 0px;
    font-size: 36px;
    line-height: 36px;
  }
  @media (min-width: @desktop-width) {
    padding-left: 170px;
    margin-bottom: 70px;
  }
}

.program-section__description {
  .second-font ();
  font-size: 14px;
  line-height: 18px;
  border-bottom: 1px solid #D9D9D9;
  margin: 0;
  padding-bottom: 23px;
  @media (min-width: @tablet-width) {
    width: 340px;
    font-size: 16px;
    line-height: 24px;
    border: none;
  }
  @media (min-width: @desktop-width) {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    width: 470px;
  }
}

.program-section__button {
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  color: @black;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 2px;
  position: relative;
  @media (min-width: @tablet-width) {
    width: 165px;
    font-size: 20px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }



  &::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 12px;
    background-image: url(../img/icons/arrow-icon.svg);
    background-repeat: no-repeat;
    background-position-x: 100%;
    top: calc(50% + 4px);
    left: 40%;
    @media (min-width: @tablet-width) {
      left: 70%;
    }

    @media (min-width: @desktop-width) {
      top: 10px;
    }
  }

  &:hover::before, &:active::before {
    width: 32px;
  }

  &:active {
    opacity: 30%;

  }

}

.program-section__title::before {
  content: "";
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: @basic-green;
  position: absolute;
  top: calc(50% - 25px);
  left: 0;
  @media (min-width: @tablet-width) {
    width: 200px;
    height: 200px;
    top: calc(50% - 15px);
    left: 60%;
  }
  @media (min-width: @desktop-width) {
    width: 100px;
    height: 100px;
    left: 0;
    top: calc(50% - 50px);
  }
}

.program-section__title--slim::after {
  content: "";
  width: 50px;
  height: 50px;
  background-image: url(../img/cat-program-slim.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: calc(50% - 30px);
  left: 5px;
  @media (min-width: @tablet-width) {
    width: 200px;
    height: 200px;
    top: calc(50% - 30px);
    left: 65%;
  }
  @media (min-width: @desktop-width) {
    width: 100px;
    height: 100px;
    top: calc(50% - 60px);
    left: 10px;
  }
}

.program-section__title--gain::after {
  content: "";
  width: 67px;
  height: 28px;
  background-image: url(../img/cat-program-stronk.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: calc(50% - 14px);
  left: -10px;
    @media (min-width: @tablet-width) {
    width: 268px;
    height: 112px;
    top: calc(50% + 40px);
    left: 55%;
  }

  @media (min-width: @desktop-width) {
    width: 134px;
    height: 100px;
    top: calc(50% - 30px);
    left: -20px;
  }
}
