.additional-products {
  border-top: 1px solid rgba(235, 235, 235, 1);
  display: grid;
  grid-template-columns: 1fr;



  .desktop({
    grid-template-columns: 895px 250px;
    column-gap: 80px;
    .desktop-wrapper;
    margin-bottom: 86px;
  })
}


.additional-products__wrapper {
  width: 280px;
  margin: 0 auto;
  .tablet({
    .tablet-wrapper;
  });
  .desktop({
    width: 100%;

      })
}


.additional-products {
  .tablet({
    border: none;
  })
}



.additional-products__title {
  .main-font();
  font-size: 24px;
  line-height: 26px;
  position: relative;
  .tablet ({
    font-size: 36px;
    line-height: 36px;
    .tablet-wrapper;
    .tablet-padding;
    br {
      display: none;
    };
    .desktop({
      .desktop-wrapper;
      padding: 0;
      margin-bottom: 80px;
    });
  });
}

// HR //
.additional-products__title::after {
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background-color: @basic-green;
  margin-left: 50px;
  top: 15px;
  .tablet ({
    top: 20px;
    margin-left: 40px;
  })
}
// HR tablet //

.additional-products__title::before {
  .tablet ({
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background-color: @basic-green;
  top: 20px;
  left: -100%;

  });
  .desktop({
    left: -1260px;
  })
}

/* CARDS */

.additional-products__card {
  .second-font;
  font-size: 14px;
  color: @special-dark-grey;
  padding-top: 10px;
  padding-bottom: 20px;
  border-top: 1px solid @special-grey;
  .tablet({
    .tablet-wrapper;
    &:last-of-type {
      border-bottom: 1px solid @special-grey;
    }
  });
  .desktop({
    width: 100%;
    display: flex;
    align-items: center;
    column-gap: 38px;
    padding: 15px 0 15px 0;
    // flex-wrap: wrap;
  })
}

.additional-products__card:last-child {
  border-bottom: 1px solid @special-grey;
}

.additional-products__card-title {
  .main-font;
  font-size: 16px;
.tablet({
  font-size: 20px;
  line-height: 20px;
})
}

.additional-products__card-link {
  color: @special-dark-grey-2;
  text-decoration: none;
  text-transform: uppercase;
}

.add-products__table {
  width: 280px;
  text-align: left;
  .tablet({
    .tablet-wrapper;
    margin-bottom: 13px;
  });
  .desktop({
    margin: 0;

  })
}

.add-products__table tbody {
  .tablet({
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

    justify-content: space-between;
  })
}

.add-products__details-wrapper {
  display: flex;
  justify-content: space-between;
  .tablet({
    font-size: 16px;
    line-height: 20px;
    column-gap: 45px;
  });
  .desktop({
    width: 300px;
    text-align: left;
    justify-self: flex-start;
  })
}

.additional-products__card .order-button {
  .tablet({
    width: 100%;
  });
  .desktop({
    margin: 0;
    width: 244px;
  })
}
