
.main-footer__map {
  border: none;
}

.main-footer__map--size {
  display: block;
  width: 100%;
  height:362px;
  margin: 0 auto;
  padding: 0;
  .tablet({
    height: 400px;
  })
}

.main-footer__coop-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
.desktop({
  max-width: 1440px;
  margin: 0 auto;
})
}

.coop-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .desktop({
    grid-column: 1 / -1;
    grid-row: 1 / 3;
    z-index: 1;
    width: 565px;
    height: 200px;
    background-color: @white;
    text-align: center;
    margin: 100px auto auto 110px;
  })
}

.main-footer__map {
  .desktop({
    grid-column: 1 / -1;
    grid-row: 1 / 3;
    z-index: 0;
  })
}
