// colors
@basic-green: #68B738;
@basic-green-2:#5EAA2F;

@basic-grey: #F2F2F2;
@special-grey-2: #EBEBEB;
@special-grey: #CDCDCD;
@special-dark-grey: #444444;
@special-dark-grey-2: #222222;

@status_error: #FF8282;

@black: #000;
@white: #fff;


// Viewports

@tablet-width: 760px;
@desktop-width: 1400px;
@mobile-width-only: 767px;

// Responsive mixins

.tablet(@rules) {
  @media (min-width: @tablet-width) {
      @rules();
  }
}

.desktop(@rules) {
  @media (min-width: @desktop-width) {
      @rules();
  }
}

.tablet-wrapper () {
  @media (min-width: @tablet-width) {
  width: 710px;
  margin-left: auto;
  margin-right: auto;
}
}

.tablet-padding () {
  @media (min-width: @tablet-width) {
  padding-left: 40px;
  padding-right: 40px;
}
}

.desktop-wrapper () {
  @media (min-width: @desktop-width) {
  width: 1220px;
  margin-left: auto;
  margin-right: auto;
}
}

.desktop-padding () {
  @media (min-width: @desktop-width) {
  padding-left: 40px;
  padding-right: 40px;
}
}

// shadows


.shadow-normal () {
background: #C4C4C4;
box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.16);
}

.shadow-hover () {
  background: #C4C4C4;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.19);
  }

.shadow-pressed () {
  background: #C4C4C4;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
  }


// * tools *//

.outline(@color) {
  outline: 3px solid fade(@color, 30%);
  outline-offset: -3px;

}

.bottom-border () {
  border-bottom: 1px solid #D9D9D9;
  }
