.example-section {
  background-color: #EAEAEA;
  @media (min-width: @tablet-width) {
    border-top: 1px solid #E6E6E6;
    background-image: linear-gradient(#fff 0, #fff 619px, #EAEAEA 462px);
  }
  @media (min-width: @desktop-width) {
    .desktop-wrapper;
    border-top: none;
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: left;
    background-image: linear-gradient(to top, #F2F2F2 462px, #fff 462px, #fff 0px);
  }
  }

.example-section__main-wrapper {
    background-image: linear-gradient(to top, #F2F2F2 462px, #fff 462px, #fff 0px);
  }

.example-section .main-content__title {
  padding-top: 25px;
}

.example-section__description {
  .second-font();
  color: @special-dark-grey;
  width: 280px;
  margin: 0 auto;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 20px;
  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    width: 710px;
    margin-bottom: 54px;
    padding-right: 20px;
  }
  .desktop({
    width: 100%;
    grid-column: 1 / 2;
    margin-bottom: 24px;
    align-self: start;
    padding-right: 150px;
  })
}

.example-data {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 34px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  text-align: center;
  @media (min-width: @tablet-width) {
    width: auto;
    margin-left: 20px;
    margin-right: auto;
    column-gap: 42px;
  }
.desktop({
  margin-left: 0;
  justify-content: flex-start;
})
}

.example-data__details {
  text-transform: uppercase;
  font-size: 24px;
  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 37px;
  }
}

.example-section__title {
  .desktop({
width: 100% !important;
  })
}

.example-data__title {
  color: @special-dark-grey;
  .second-font;
  text-transform: lowercase;
  font-size: 12px;
  display: block;
  background-color: #EAEAEA;
  position: absolute;
  width: 78px;
  height: 24px;
  bottom: auto;
  left: calc(50% - 39px);
  @media (min-width: @tablet-width) {
    background-color: white;
    bottom: -10px;
    br {
      display: none;
    }
  }
  .desktop({
    background-color: #f2f2f2;
  })
}

.example-data__item {
  text-transform: uppercase;
  position: relative;
  color: @black;
  font-size: 14px;
  .main-font;
  width: 124px;
  height: 56px;
  border: 1px solid #CDCDCD;
  border-radius: 3px;
  @media (min-width: @tablet-width) {
    padding: 15px;
    width: 162px;
    height: 72px;
  }

}

.example-data__cost {
  text-transform: uppercase;
  display: block;
  color: @black;
  font-size: 14px;
  .main-font;
  width: 200px;
  margin: 0 auto;
  margin-bottom: 35px;
  br {
    display: none;
  }

  @media (min-width: @tablet-width) {
    width: 174px;
    display: inline-block;
    text-align: right;
    padding-top: 15px;
    br {
      display: inline;
    }
  }
  .desktop({
    br {
      display: none;
    }
    width: 100%;
    text-align: left;

    .money {
      margin-left: 63px;
    }
  })
}


.example-data__slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  .desktop({
    width: 440px;
    grid-column: 2 / 3;
    grid-row: 1 / 4;
    margin-left: 93px;
  })
}

.slider__prev, .slider__next {
  text-transform: uppercase;
}

.example-data__cat {
  width: 280px;
  height: 232px;
  @media (min-width: @tablet-width) {
    margin-top: 40px;
    height: 511px;
    width: auto;
  }
}

.slider__toggles {
  display: flex;
  width: 80%;
  justify-content: space-between;
}

.example-data__wrapper {
  @media (min-width: @tablet-width) {
    width: 710px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr auto;
  }
  .desktop({
    grid-template-columns: 1fr;
    grid-column: 1 / 2;

  })
}
