.copyright-section {
  .second-font;
  background-color: @basic-grey;
  .tablet ({})

}


.copyright-section__link {
  width: 280px;
  height: 80px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: @special-dark-grey;
  @media (min-width: @tablet-width) {
    width: 163px;
    height: 40px;
  }

  &:hover {
    .copyright-section__image {
    color: @basic-green;
  }
  }

  &:active {
    opacity: 30%;
  }
}
