// }

.form-field__required-link {
  color: @black;
  text-decoration: none;
}

.form-field__primal-data {
  text-transform: uppercase;
  .main-font;
  color: @special-dark-grey;
  border: none;
  width: 280px;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 24px;
  .tablet-wrapper;
  .tablet-padding;
  .desktop({
    padding: 0;
    margin: 0;
    max-width: 100%;
  })
}

.form-field__form input {
  text-transform: uppercase;
  .main-font;
  color: @special-dark-grey;
}

.form-field__options {
  text-transform: uppercase;
  .main-font;
  color: @special-dark-grey-2;
  border-left: none;
  border-right: none;
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  padding-top: 20px;
  padding-bottom: 20px;
  .tablet({
    border: none;
    .tablet-wrapper;
    .tablet-padding;
  });
  .desktop({
    padding: 0;
    margin: 0;
    max-width: 570px;
  })

}

// inputs common //

.form-common-input {
  border-radius: 0;
  border: 1px solid #E7E7E7;
&:hover {
  border: 1px solid #CDCDCD;
}
&:focus {
  border-color: #444444;
};
&:focus:invalid {
  border-color: #FF8282;
};
}

.form-common-input::placeholder {
  color: @special-dark-grey;
}

.form-field__title {
  font-weight: 400;
  font-size: 24px;
  .tablet({
    .tablet-wrapper;
  });
  .desktop({
    margin-top: 70px;
    margin-bottom: 76px;

  })
}

.form-field__form {
  display: grid;
  grid-template-columns: 1fr;
}

.form-field__addition-list {
  display: grid;
  grid-template-columns: 1fr;
  .tablet({
    grid-template-columns: 1fr 1fr;
  });

  .desktop({
    grid-template-columns: 1fr 1fr 1fr 1fr;
  });
}



.form-field__send-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;

  .tablet({
    grid-template-columns: 1fr 1fr;
    justify-items: flex-start;
    .tablet-wrapper;
    .tablet-padding;
    margin-bottom: 72px;
  });

  .desktop({
    grid-template-columns: 490px auto;
    justify-items: flex-start;
    .desktop-wrapper;
    column-gap: 80px;
    padding: 0;
  });
}

.form-field__program-wrapper {
  display: grid;
  grid-template-columns: 1fr;



  .desktop({
    grid-template-columns: 570px 570px;
    column-gap: 80px;
    .desktop-wrapper;
    padding: 0;
    margin: 0;
  });
}

.form-field__submit {
  .desktop({
    width: 100%;
  })
}

//* primal data placing *//

.form-field__name, .form-field__weight, .form-field__age {
  display: flex;
  align-items: center;
}

.form-field__name-input, .form-field__weight-input, .form-field__age-input {
  flex-grow: 1;
  margin-left: 12px;
  height: 45px;
  padding-left: 15px;

}

.form-field__weight, .form-field__age {
  margin-top: 12px;
}

// program options //

.form-field__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 280px;
  margin: 0 auto;
  .tablet({
    width: 630px;
    padding: 25px 30px;
    border: 1px solid @special-grey;
  });
  .desktop({
    max-width: 570px;
    padding-top: 22px;
    padding-bottom: 22px;
    padding-left: 30px;
    padding-right: 30px;
  })
}

.form-field__item + .form-field__item {
  margin-top: 13px;
}

.form-field__item-label {
  display: flex;
  flex-direction: row;
align-items: center;
}

// custom marks //
.form-field__item-label {
  cursor: pointer;
  font-size: 16px;
  line-height: 18px;

}

.control-label {
  display: inline-block;
  padding-left: 16px;
  color: @special-dark-grey-2;
}

.control-mark {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #CDCDCD;
  position: relative;


}


.form-field__radio:checked + .control-mark::after {
  position: absolute;
  content: '';
  width: 14px;
  height: 14px;
  top: 25%;
  left: 25%;
  border-radius: 50%;
  background-color: @basic-green;
  }

  .form-field__item-label:active {
    opacity: 30%;
  }

.form-field__item-label:hover .control-mark {
  border-color: #bababa;
}

// Contacts //
.form-field__contacts-list {
  padding: 0;
}

.form-field__contacts {
  border: none;
  padding: 0;
}

.form-field__contacts-list {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;

  .tablet({
    justify-items: flex-start;
    .tablet-wrapper;
    .tablet-padding;
  });

  .desktop({
    grid-template-columns: 570px 570px;
    column-gap: 80px;
    justify-items: flex-start;
    .desktop-wrapper;
    padding: 0;
  });
}

.form-field__contacts .form-field__title {
  font-size: 24px;
  line-height: 26px;
  margin-left: 20px;
  position: relative;
  .tablet ({
    font-size: 36px;
    line-height: 36px;
    .tablet-padding;
    .tablet-wrapper;
    br {
      display: none;
    };
    .desktop({
      .desktop-wrapper;
      padding-left: 0;
    });
  })
}

// HR //
.form-field__contacts .form-field__title::after {
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background-color: @basic-green;
  margin-left: 50px;
  top: 15px;
  .tablet ({
    top: 20px;
    margin-left: 40px;
  })
}
// HR tablet //

.form-field__contacts .form-field__title::before {
  .tablet ({
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background-color: @basic-green;
  top: 20px;
  left: -100%;

  });
  .desktop({
    left: -1260px;
  })
}

.form-field__contacts-item-label {
  color: @special-dark-grey-2;
}

.form-field__contacts-list {
  list-style-type: none;
}

.form-field__contacts-item-label {
  display: flex;
  align-items: center;
  width: 280px;
  .tablet ({
    width: 550px;
  });
  .desktop({
    width: 570px;
  })
}

.form-field__input  {
  flex-grow: 1;
  margin-left: 12px;
  height: 45px;
  padding-left: 15px;

}

.form-field__contacts-item-label , .form-field__contacts-item-label  {
  margin-top: 15px;
  position: relative;
}

.form-field__email, .form-filed__tel {
  .tablet({
    flex-grow: 1;
  })
}

.form-field__contacts-item-label::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  top: 13px;
  right: 13px;
}

.form-field__contacts-item-label--email::before {
  background-image: url(../img/icons/email_icon.svg);
}

.form-field__contacts-item-label--tel::before {
  background-image: url(../img/icons/phone-icon.svg);
  right: 10px;
}

.form-field__contacts-item-label--email:focus-within::before {
  background-image: url(../img/icons/email-green.svg);
}

.form-field__contacts-item-label--tel:focus-within::before {
  background-image: url(../img/icons/phone-green.svg);
}

