    background-color: @basic-green;
    top: 20px;
    left: -100%;

    });
    .desktop({
      width: 100%;
      left: -1260px;
    })
  }

.form-field__commentary-field {
  width: 320px;
  margin: 0 auto;
  .tablet ({
    width: 710px;
  });
  .desktop({
    .desktop-wrapper;
  })
}

.form-field__commentary {
  display: block;
  width: 280px;
  min-height: 150px;
  margin: 0 auto;
  padding: 5px 10px;

  font-size: 16px;
  line-height: 18px;

  .main-font ();

  .tablet({
    width: 630px;
    margin: 0 auto;
  });
  .desktop({
    .desktop-wrapper;
  })
}


