.form-field__addition-list {
  margin: 0 auto;
  margin-top: 34px;
  margin-bottom: 25px;
  padding: 0;
  list-style-type: none;
  width: 280px;
  .tablet({
    .tablet-wrapper;
    .tablet-padding;
  });
  .desktop({
    .desktop-wrapper;
    padding: 0;
  })
}

.form-field__addition-item-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  &:disabled {
    opacity: 50%;
  };
}

.form-field__addition-item-label:hover .control-check-mark {
  border-color: #bababa;
}

.control-check-label {
  margin-left: 16px;

}

.form-field__addition-item + .form-field__addition-item {
  margin-top: 12px;
}

.control-check-mark {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: 1px solid rgba(205, 205, 205, 1);
  position: relative;
}



.form-field__check:checked + .control-check-mark::after {
  position: absolute;
  content: '';
  width: 30px;
  height:30px;
  background-image: url(../img/icons/checkmark.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.form-field__required-comment {
  .second-font;
  font-size: 14px;
  line-height: 16px;
  margin-top: 20px;
  margin-bottom: 32px;
}
