.catalog {
  width: auto;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(235, 235, 235, 1);

  .tablet({
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    padding-left: 30px;
    padding-right: 30px;
    border-bottom: none;
  });

  .desktop({
    width: 1220px;
    padding: 0;
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
    margin-bottom: 40px;

  });


  // @media (min-width: @tablet-width) {
