.how-section {
  min-width: 320px;
  padding-left: 20px;
  padding-right: 20px;

}

.how-section__list {
  display: grid;
  row-gap: 20px;
  width: 280px;
  padding: 0px;
  margin: 0 auto;
  margin-bottom: 45px;
  padding-left: 80px;
  list-style-type: none;


  @media (min-width: @tablet-width) {
    width: 710px;
    margin-top: 70px;

  grid-template-columns: 1fr 1fr;
  row-gap: 37px;
  column-gap: 150px;
  padding-left: 0;
  }

  @media (min-width: @desktop-width) {
    .desktop-wrapper;
    display: grid;
    grid-template-columns: repeat(4; 1fr);
    column-gap: 80px;

  }
}

.how-section__item {
  .second-font();
  position: relative;
  color: @special-dark-grey;
  padding: 0px;
  font-size: 14px;
  line-height: 18px;
  @media (min-width: @tablet-width) {
    padding-top: 110px;
    padding-bottom: 57px;
    padding-right: 70px;

  }
  @media (min-width: @desktop-width) {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0em;

    padding-right: 0px;

  }
}

