// }

.how-section__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: @basic-green;
  position: absolute;
  left: -80px;
  @media (min-width: @tablet-width) {
    width: 80px;
    height: 80px;
    left: 0;
    top: 0;
  }

}

.how-section__item:nth-child(1)::before {
  background-image: url(../img/icons/leaf-icon.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.how-section__item:nth-child(2)::before {
  background-image: url(../img/icons/icon_tub.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.how-section__item:nth-child(3)::before {
  background-image: url(../img/icons/icon_eat.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.how-section__item:nth-child(4)::before {
  background-image: url(../img/icons/icon_alarm.svg);
  background-position: center;
  background-repeat: no-repeat;
}

@media (min-width: @tablet-width) {
.how-section__item::after {
  content: "";
  .main-font;
  width: 112px;
  height: 280px;
  position: absolute;
  right: 70px;
  top: -40px;
  font-size: 280px;
  line-height: 280px;
  opacity: 10%;
}
}

@media (min-width: @desktop-width) {
  .how-section__item::after {
    right: 0;
  }

}

.how-section__item:nth-child(1)::after {
  content: "1";
}

.how-section__item:nth-child(2)::after {
  content: "2";
}

.how-section__item:nth-child(3)::after {
  content: "3";
}

.how-section__item:nth-child(4)::after {
  content: "4";
}
