.page-navigation {
  .main-font();
  position: relative;
    display: flex;
    flex-direction: column;

    @media (min-width: @tablet-width) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .desktop-wrapper;

      margin-bottom: 30px;
    }

}

@media (max-width: 760px) {
.page-navigation--inner-page {
  .bottom-border;
}
}


.page-navigation__main-menu {

  list-style-type: none;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
  margin: 0 auto;


  @media (min-width: @tablet-width) {
  display: flex;
  flex-wrap: wrap;
  width: 440px;
  justify-content: space-between;



}

@media (min-width: @desktop-width) {
  display: flex;
  justify-content: space-between;
  width: 480px;
  margin-left: 530px;


}
}

.main-menu__link {
  padding: 0;
  margin: 0;
  text-decoration: none;
  font-weight: 500;
  background-color: @white;
  color: @black;
  display: block;
  padding: 22px 24px;
  font-size: 20px;
  line-height: 20px;
  white-space: nowrap;

  @media (min-width: @tablet-width) {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding: 10px;
    position: relative;
    z-index: 1;
    background-color: transparent;
    transition: .2s;
    &:hover {
      text-shadow: rgba(19, 19, 19, 0.086) 0px 4px 6px;
      opacity: 60%;
    };
    &:active {
      opacity: 30%;
    }
  }

  @media (min-width: @desktop-width) {
    font-weight: 400;
    position: relative;
    z-index: 1;
  }

}


.main-menu__link--current {
  position: relative;
}

.main-menu__link--current::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    content: '';
    height: 2px;
    bottom: 0;
    left: 10px;
    right: 10px;
    background-color: @basic-green;
  }
}

.index-nav .main-menu__link--current::before {
  @media (min-width: @desktop-width) {

    position: absolute;
    content: '';
    height: 2px;
    bottom: 0;
    left: 10px;
    right: 10px;
    background-color: @white;
  }
}


.index-nav-link {
  @media (min-width: @desktop-width) {
    color: rgb(255, 255, 255);

  }
}

.main-menu__item {
  border-top: 2px solid #E6E6E6;



  @media (min-width: @tablet-width) {
    border: none;
  }


}

.main-menu__item:last-child {
  border-bottom: 2px solid #E6E6E6;
  @media (min-width: @tablet-width) {
    border: none;
  }
}



/* logos */

.main-logo, .text-logo {
  text-decoration: none;

}

.page-navigation__logo-wrapper {
  width: 87.5%;
  margin: 0 auto;
  min-height: 65px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  transition: .2s;
&:hover {
  opacity: 80%;
}
&:active {
  opacity: 60%;
}

  @media (min-width: @tablet-width) {
    justify-content: flex-start;
    width: 174px;
    .page-navigation__text-logo {
      margin-left: 12px;

    }
    .page-navigation__toggle-button {
      display: none;
    }

    @media (min-width: @desktop-width) {
      justify-content: flex-start;
      width: 202px;
      .page-navigation__text-logo {
        margin-left: 14px;
      }
      .page-navigation__toggle-button {
        display: none;
      }
    }


  }
}

/* no-js */




// .page-navigation__toggle-button {
//   display: block;
// }




.page-navigation--closed .page-navigation__toggle-button {

  position: relative;
  min-width: 25px;
  min-height: 25px;

  cursor: pointer;


  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: @black;
    box-shadow: 0px 8px 0px 0px black, 0px 16px 0px 0px black;

  }
}


.page-navigation--opened .page-navigation__toggle-button {

  position: relative;
  min-width: 25px;
  min-height: 25px;

  cursor: pointer;


  &::before, &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    height: 1.5px;
    width: 100%;
    background-color: @black;
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}


@media (max-width: 760px) {
.page-navigation--opened .page-navigation__main-menu {
  position: absolute;
  top: 100%;
  height: 100%;
  transform: scaleY(1);
  z-index: 10;
  transition: .1s;
  transform-origin: top;
  // .outline(red);
  transition-property: transform;
}



.page-navigation--closed .page-navigation__main-menu {
  transform: scaleY(0);

  height: 0;
}

.page-navigation--no-js .page-navigation__main-menu {
  display: block;
  position: static;
}

.page-navigation--no-js .page-navigation__toggle-button {
  display: none;
}
}

