.main-content__title, .main-content__slogan {
  @media (min-width: @tablet-width) {

  color: black;
}
}

.promo-food {
  display: flex;
  min-height: 480px;
  flex-direction: column;
  align-items: center;
  text-align: center;
  // background-position: top;
  // background-image: url(../img/background/background-cat.jpg);
  background-position: 50% 0;
  background-color: #63a63b;
  background-image: url(../img/background/promo-small.jpg), linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 20px;
  @media (min-width: @tablet-width) {
    width: 710px;
    margin: 0 auto;
    background-color: transparent;
    text-align: left;
}
  @media (min-width: @desktop-width) {
    .desktop-wrapper;
    display: grid;
    grid-template-columns: 420px auto;
    margin-bottom: 82px;
}
}

.promo-food__button {
  align-self: stretch;
  order: 3;
  margin: 0 auto;
  max-width: 320px;
  .tablet({
    margin: 20px 87px;
    max-width: 244px;
    text-align: center;
  });
  .desktop({
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    align-self: start;
    grid-column: 1 / 2;
  })

}

@media (min-width: @tablet-width) {
  .promo-food .main-content__title, .promo-food .main-content__slogan {
    align-self: start;
    align-items: flex-start;
    padding-left: 87px;
    margin-left: 0;

  }

  .promo-food .main-content__title {
    width: 414px;

  }



  .goods-preview {
    order: 4;
    margin-top: -100px;
    width: 710px;
    height: 610px;

  }

}
@media (min-width: @desktop-width) {
  .promo-food .main-content__title, .promo-food .main-content__slogan {
    align-self: start;
    align-items: flex-start;
    padding-left: 0px;
    margin-left: 80px;

  }

  .promo-food .main-content__title {
    width: 414px;
font-size: 60px;
font-weight: 400;
line-height: 60px;
letter-spacing: 0em;
text-align: left;
padding-left: 0px;
margin-left: 80px;
margin-top: 100px;
  }



  .goods-preview {
    order: 4;
    width: 552px;
    height: 500px;
    grid-row: 1 / 5;
    grid-column: 2 / 3;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;
  }

}

.desktop({
  .goods-preview {
    transition: 1s cubic-bezier(.68,-0.55,.22,1.31);
  }

  .goods-preview:hover {
    transform: scale(110%);
  };
});


