@media (min-width: @tablet-width) {
  .promo-food {
    background-image: none;
   }

  .promo-food::before {
    background-color: transparent;
  }
}

.promo-food::after {
  display: none;
  content: "";
  position: absolute;
  top: -100%;
  left: 50%;
  width: 50%;
  height: 200%;
  background-color: @basic-green;
  @media (min-width: @desktop-width) {
    display: block;
    width: 1000px;
    background-image: url(../img/background/promo-large.jpg);
    background-repeat: no-repeat;
    background-position: 0% 100%;
  }
}


.main-content__title, .main-content__slogan, .goods-preview, .button {
  position: relative;
  z-index: 2;
}

.promo-food__slogan {
  .desktop({
font-size: 20px;
font-weight: 400;
line-height: 20px;
letter-spacing: 0em;
grid-column: 1 / 2;

  })
}
