.product-card__title {
  .main-font;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: auto;
  color: @special-dark-grey-2;
  .tablet ({
    & span {
      display: block;
      text-align: center;
    }
    margin-top: 35px;
    font-size: 20px;
    line-height: 24px;
  })
}

.product-card__link {
  text-decoration: none;
  color: @black;
  .tablet ({
    margin-top: 30px;
  })
}

.product-card__info {
  .second-font;
  font-size: 12px;
  .tablet ({
    font-size: 16px;
    line-height: 16px;
    align-self: stretch;
  })
}

/* cards */

.product-card {
  border-top: 1px solid rgba(235, 235, 235, 1);
  .tablet({
    // .outline(black);
    display: flex;
    border: none;
    min-height: 510px;
    // max-width: 330px;
    padding-bottom: 40px;
    background-color: @basic-grey;
    background-image: linear-gradient(to bottom, @white 80px, @basic-grey 80px);
  });
  .desktop({
    width: 245px;
  })
}




.product-card__content-wrapper {
  width: 280px;
  min-height: 184px;
  margin: 0 auto;
  padding-top: 20px;
  display: grid;
  grid-template-columns: 50% 80px auto;
  .tablet ({
    // .outline(red);
    padding-top: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    min-height: auto;
  });
  .desktop({
    width: 168px;
  })
}


.product-card__link {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
}


.product-card__picture,
.product-card__image {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  margin: auto;
  .tablet ({
    margin-top: 10px;

    order: -1;
  })
}

.product-card__image {
  .tablet({
    // .outline(red);
    width: 100%;
    height: 100%;

  })
}



.product-card__info {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
  color: @special-dark-grey;
  .tablet ({
    margin-top: 20px;
    margin-bottom: 35px;
  })
}

.order-button {
  grid-column: 1 / 2;
  grid-row: 3 / 4;
  margin-top: 12px;
  // .tablet ({
  //   margin-top: 35px;
  // });
  .desktop({

    min-width: 168px;
  })
}

.product-card__row {
  display: flex;
  justify-items: center;
  justify-content: space-between;
  .tablet ({
    border-bottom: 1px solid @special-grey;
    padding-top: 7px;
    padding-bottom: 7px;
  })
}

.product-card:last-of-type {
  border-bottom: 1px solid rgba(235, 235, 235, 1);
  padding-bottom: 30px;
  .tablet({
    border: none;
    padding-bottom: 40px;
  })

}
