.form-page {
  .main-font;
  overflow-x: hidden;
}

.form-page__heading {
  color: @black;
  .tablet-wrapper ();
  .tablet-padding;
  .desktop({
    .desktop-wrapper;
    padding: 0;
    margin-top: 0;
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 54px;
  })
}

.form-page__text {
  font-size: 16px;
  text-transform: uppercase;

}

.form-page__description {
  color: @black;
  font-size: 14px;
  width: 250px;
  .tablet-wrapper ();
  .tablet-padding;
  .desktop({
    .desktop-wrapper;
    padding: 0;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 60px;
  })
}


