.show-more {
  width: 100%;
  .tablet({
    margin-top: 80px;
    padding: 38px 38px 38px 38px;
    border: 2px solid rgba(242, 242, 242, 1);
  })
}

.show-more__title {
  color: #111;
  .main-font;
  text-transform: uppercase;
  padding-top: 23px;
  margin: 0;
  .tablet({
    font-size: 20px;
    line-height: 22px;
    width: 140px;
    text-align: center;
  });

}

.show-more__description {
  .second-font;
  color: @special-dark-grey;
  font-size: 12px;
  .tablet({
    font-size: 16px;
    line-height: 18px;
    width: 170px;
    text-align: center;
  })
}

.show-more__wrapper {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  .tablet({
    height: 100%;
    justify-content: space-between;
  });
  .desktop({
    width:100%;
  })
}

.show-more__button {
  border: none;
  background-color: transparent;
  width: 30px;
  height: 30px;
  background-image: url(../img/icons/plus.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 25px;
  cursor: pointer;
  .tablet({
    width: 60px;
    height: 60px;
    background-size: cover;
  })


}

.show-all-button {
  cursor: pointer;
  background-color: #F2F2F2;
  color: #444;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  margin-top: 20px;
  margin-bottom: 25px;
  .tablet({
    margin-bottom: 0;
  });
  &:hover {
    background-color: #EBEBEB;
  };
  &:active {
    color: rgba(68, 68, 68, 0.305);
  }
}
