.coop-section {
width: 280px;
margin: 0 auto;
@media (min-width: @tablet-width) {
  padding-top: 60px;
  padding-bottom: 56px;
  width: 628px;
}
.desktop({
  width: 565px;
  padding-left: 80px;
  padding-right: 80px;

})
}



.coop-section__heading {
  color: #111;
  text-transform: uppercase;
  .main-font;
  font-size: 16px;
  line-height: 20px;
  margin-top: 26px;
  margin-bottom: 26px;
  @media (min-width: @tablet-width) {
    width: 160px;
    font-size: 20px;
    line-height: 26px;
    margin: 0;
  }
  .desktop({
    text-align: left;
  })
}

.coop-section__adress {
  .second-font;
  font-style: normal;
  color: @special-dark-grey;
  font-size: 14px;
  line-height: 20px;
  @media (min-width: @tablet-width) {
    width: 160px;
    font-size: 16px;
    text-align: right;
  }
  .desktop({
    text-align: left;
  })
}

.coop-form-wrapper {
  background-color: @basic-grey;
}
