.promo {
  padding-top: 35px;
  padding-top: 35px;
  .tablet({
    padding-top: 70px;
    padding-bottom: 70px;
  });
  .desktop({
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    padding: 0;
    margin-top: 160px;

  })
}

.promo__text {
  .second-font;
  color: @white;
  z-index: 2;
  position: relative;
  font-size: 16px;
  width: 160px;
  margin: 0 auto;
  margin-top: 146px;
  .tablet({
    margin-top: 70px;
    margin-bottom: 70px;
    margin-right: 124px;
    text-align: left;
  });
  .desktop({
    margin: 0;
    text-align: center;
  })
}

.promo-wrapper {
  text-align: center;
  background-position: top;
  background-image: url(../img/background/backpack-cat.png);

  padding: 20px;
  position: relative;
  width: 280px;
  height: 290px;
  margin: 0 auto;
  margin-bottom: 35px;
  .tablet({
    width: 710px;
    height: 200px;
    padding: 1px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: -350px;

  });
  .desktop({
    width:100%;
    height: 300px;
    margin-bottom: 0;
    background-position-y: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 42px;
  })

}

// @media (min-resolution: 2dppx) {
//   .promo-wrapper {
//   background-image: url(../img/background/backpack-cat@2x.png);
//   }
// };

.promo-wrapper::before{
  background-color: @basic-green;
  opacity: 85%;
  content: '';
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
}

.promo-wrapper::after {
  content: '';
  position: absolute;
  background-image: url(../img/icons/icon_gift.svg);
  width: 80px;
  height: 80px;
  top: 60px;
  left: calc(50% - 40px);
  .tablet({
    top: 57px;
    left: 77px;
  })
}
