.contacts-section {
  text-align: center;
  background-color: @basic-grey;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  @media (min-width: @tablet-width) {

    flex-direction: row;
    align-items: center;
    width: 710px;
    margin: 0 auto;
    padding: 25px 30px 25px 15px;
    .text-logo {
      padding: 0;
    }
  }
  .desktop({
    .desktop-wrapper;
  })
}

.contacts-section__wrapper {
  width: 100%;
  background-color: @basic-grey;
}

.contacts-section__footer-logo {
  margin: 30px;
}

.contacts-section__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  width: 280px;
  height: 63px;
  margin: 0 auto;
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  align-items: center;
  justify-content: center;
  @media (min-width: @tablet-width) {
    border: none;
  }

}

.contacts-section__item + .contacts-section__item {
  margin-left: 35px;
}

.coop-section__city {
  .tablet({
    display: block;
    padding-top: 20px;
  })
}

.contacts-section__link {
  color: #666666;
}

.contacts-section__link:hover {
  color: @basic-green;
}

.contacts-section__link:active {
  opacity: 30%;
}

