


.main-content__title {
  .main-font ();
  font-size: 36px;
  line-height: 40px;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 40px;
  @media (min-width: @tablet-width) {
    width: 710px;
    font-size: 60px;
    line-height: 60px;
    padding: 0;
  }
  @media (min-width: @desktop-width) {
    width: 1220px;
    margin-bottom: 70px;
  }
}

.heading-title {
  .main-font();
  color: @white;
  font-size: 36px;
  line-height: 36px;
  margin: 25px 20px;

}

.heading-text {
  .main-font();
  color: @white;
  font-size: 14px;
  text-transform: uppercase;
  margin: 0px;
  padding: 0px;
  padding-bottom: 30px;
  margin-left: 20px;
}

.page-body {
  min-width: 320px;
  margin: 0 auto;
}




